.cards {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 165px;
    margin-right: 165px;
}

.pos {
    display: flex;
    flex-direction: row;
    width: 255px;
    height: 88px;
    background-color: white;

    /* Neutral / White */

    background: #FFFFFF;
/* card / floating */

    box-shadow: 0px 12px 26px rgba(16, 30, 115, 0.06);
    border-radius: 8px;
}

.img {
    width: 40px;
    height: 40px;
    margin: 24px;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 20px 24px 4px 0;
}

.info div:nth-of-type(2n) {
    color: #336CFB;
}

